import {getGenres, setVideogame, setVideogameErrors} from '../redux/actions/index.js'
import {useDispatch,useSelector} from 'react-redux'
import { useEffect } from 'react';


export default function VideogameCreate(){
    const dispatch=useDispatch();
    const genres=useSelector(state=>state.genres)
    const newVideogame= useSelector(state=>state.newVideogame)
    const newVideogameErrors=useSelector(state=>state.newVideogameErrors)
    const newVideogameGenres=[]
    useEffect(()=>{
        dispatch(getGenres())
    },[])

    function handleInputChange(e){
        dispatch(setVideogame())
        dispatch(validate(setVideogameErrors(newVideogame)))
    }
    function handleSelectChange(e){

    }

    return (
        <div>
            <h1>Crear videojuego</h1>
            <label>Nombre: </label><input value={newVideogame.name} onChange={handleInputChange}/>
            <label>Descripcion: </label><input value={newVideogame.description} onChange={handleInputChange}/>
            <label>Rating: </label><input value={newVideogame.rating} onChange={handleInputChange}/>
            <label>Fecha de lanzamiento: </label><input value={newVideogame.released} onChange={handleInputChange}/>
            <label>Imagen(URL): </label><input value={newVideogame.image} onChange={handleInputChange}/>
            <form action={handleSelectChange}>
                <label>Generos: </label>
                    <select >
                        {genres.map((elem)=>{
                            return(<option value={elem.id}>{elem.name}</option>)
                        })}
                    </select>
                <input type='submit' name='Agregar genero' />
            </form>
            <label>Generos</label>
            <ul>{
                newVideogameGenres.map((elem)=>{
                    return(<li>{genres[elem]}</li>)
                })
            }</ul>
            <label>Plataformas: </label>
        </div>
    )
}

function validate(input){
    let errors={};
    if(!input.name) errors.name='Nombre requerido'
    if(!input.description) errors.description='Descripcion requerida'
    if(input.rating>5) errors.rating='El maximo rating es 5'
    if(input.rating<0) errors.rating='El minimo rating es 0'
    if(input.image){
        if(typeof input.image!=='string' || input.match(/^http[^\?]*.(jpg|jpeg|gif|png|tiff|bmp)(\?(.*))?$/gmi) !== null){
            errors.image='URL incorrecto, debe ser una imagen'
        }
    }
    return errors
}