import { Component } from "react";

export default class VideogameCard extends Component{
    constructor(props){
        super(props)
        console.log(props)
    }
    render(){
        return(
           <div>
               <p>{this.props.nombre}</p>
               <img src={this.props.imagen} alt="Imagen" />
               <ul>
                  {this.props.generos.map((elem)=>{
                      return(<li>{elem.name}</li>)
                  })}
               </ul>
           </div> 
        )
    }
}

