import React,{ Component } from "react";
import {connect} from 'react-redux'
import VideogameCard from './VideogameCard.jsx'
import {getAllVideogames} from '../redux/actions/index.js'

class Home extends Component{
    
    componentDidMount(){
        this.props.getAllVideogames();
        console.log(this.props.videogames);
        //const primer=this.props.videogames[0]; 
    }
    render(){
        return(
            <div>
                <h1>Home</h1>
                {/* <VideogameCard nombre={this.primer.name} imagen={this.primer.image} generos={this.primer.genres}   /> */}
            </div>
        )
    }
}

function mapStateToProps(state){
    return{
        videogames:state.videogames
    }
}

function mapDispatchToProps(dispatch){
    return{
        getAllVideogames: ()=>dispatch(getAllVideogames())
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Home)